
html {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
.lottie {
  position: absolute;
  top: -10%;
  left: 50%;
  transform: translate(-50%, 0);
}
#fullScreen {
  position: absolute;
  height: 100%;
  width: 100%;
}
#app {
  width: 100%;
  height: 100%;
}
.loading {
  position: absolute;
  width: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 12px;
  background-color: #a9a7a6;
}
.loadingBar {
  border-radius: 12px;
  position: relative;
}
.loadingProgress {
  position: absolute;
  bottom: 30%;
  left: 50%;
  transform: translate(-50%, 0);
  font-family: monospace;
  font-size: 28px;
  font-weight: bold;
}



